// --- all fonts for the project ---

@font-face {
  font-family: "Kaushan Script";
  font-display: swap;
  src: url("../fonts/KaushanScript-Regular.woff2") format("woff2"),
    url("../fonts/KaushanScript-Regular.woff") format("woff"),
    url("../fonts/KaushanScript-Regular.ttf") format("ttf");
  font-weight: normal;
  font-style: normal;
}

